import { useEffect } from 'react'
import { 
  ref, 
  onValue, 
  set, 
  get,
  onDisconnect,
  serverTimestamp
} from 'firebase/database'
import { database } from '../config/firebase'
import { useAuthStore } from '../store/authStore'
import { useChatStore } from '../store/chatStore'
import { cleanupStaleUsers, startPeriodicCleanup } from '../utils/cleanupUsers'

export const useOnlineUsers = () => {
  const { user } = useAuthStore()
  const { setOnlineUsers } = useChatStore()

  useEffect(() => {
    const initializeOnlineUsers = async () => {
      if (!user) {
        console.log(' ')
        return
      }

      console.log(' :', user.uid, user.email || '')

      // 
      const setOnlineStatus = async () => {
        try {
          const userRef = ref(database, `users/${user.uid}`)
          const userOnlineRef = ref(database, `users/${user.uid}/isOnline`)
          const lastSeenRef = ref(database, `users/${user.uid}/lastSeen`)
          
          // 
          console.log(' ...')
          const userSnapshot = await get(userRef)
          const existingData = userSnapshot.val() || {}
          
          console.log(' :', existingData)
          console.log(' :', {
            uid: user.uid,
            email: user.email,
            nickname: user.nickname || user.displayName,
            isAnonymous: user.isAnonymous
          })
          
          // 
          await set(userRef, {
            ...existingData, // 
            isOnline: true,
            lastSeen: serverTimestamp(),
            email: user.email || existingData.email || '',
            nickname: user.nickname || user.displayName || existingData.nickname || '',
            uid: user.uid,
            isAnonymous: user.isAnonymous || false
          })
          
          console.log(' ')
          
          //  - 
          if (user.isAnonymous) {
            console.log('  - ')
            await onDisconnect(userRef).remove()
          } else {
            console.log('  - ')
            await onDisconnect(userOnlineRef).set(false)
            await onDisconnect(lastSeenRef).set(serverTimestamp())
          }
          
        } catch (error) {
          console.error(' :', error)
        }
      }
      
      // 
      await setOnlineStatus()
      
      // 
      await cleanupStaleUsers()
      
      // 
      const stopPeriodicCleanup = startPeriodicCleanup()
      
      // 
      const allUsersRef = ref(database, 'users')
      const unsubscribe = onValue(allUsersRef, (snapshot) => {
        try {
          const usersData = snapshot.val()
          if (usersData) {
            const onlineUsers = {}
            
            const now = Date.now()
            const ACTIVE_TIME = 2 * 60 * 1000 // 2
            
            Object.entries(usersData).forEach(([uid, userData]) => {
              if (userData && userData.isOnline) {
                // 
                const isRecentlyActive = userData.lastSeen && (now - userData.lastSeen < ACTIVE_TIME)
                
                if (isRecentlyActive) {
                  console.log(` : ${uid} - ${userData.nickname || userData.email || ''}`)
                  onlineUsers[uid] = {
                    uid,
                    nickname: userData.nickname || userData.email || '',
                    email: userData.email || '',
                    isOnline: userData.isOnline,
                    lastSeen: userData.lastSeen,
                    isAnonymous: userData.isAnonymous || false
                  }
                } else {
                  console.log(`  ${uid} `)
                }
              }
            })
            
            console.log(' :', Object.keys(onlineUsers))
            setOnlineUsers(onlineUsers)
          } else {
            console.log(' ')
            setOnlineUsers({})
          }
        } catch (error) {
          console.error(' :', error)
        }
      })

      // 
      return () => {
        console.log(' ')
        unsubscribe()
        stopPeriodicCleanup()
      }
    }

    let cleanup = null
    
    initializeOnlineUsers().then((cleanupFn) => {
      cleanup = cleanupFn
    })
    
    // 
    return () => {
      if (cleanup && typeof cleanup === 'function') {
        cleanup()
      }
    }
  }, [user?.uid, setOnlineUsers])
}