import React, { useState, useEffect } from 'react'
import { X, Bell, BellOff, User, Camera, Volume2, VolumeX, Smartphone } from 'lucide-react'
import { useAuthStore } from '../../store/authStore'
import { fcmManager } from '../../utils/fcmManager'
import toast from 'react-hot-toast'

const SettingsModal = ({ isOpen, onClose }) => {
  const { user, setUser } = useAuthStore()
  const [nickname, setNickname] = useState(user?.nickname || '')
  const [avatar, setAvatar] = useState(user?.avatar || '')
  const [tempNickname, setTempNickname] = useState(nickname)
  
  // 
  const [soundEnabled, setSoundEnabled] = useState(
    localStorage.getItem('notificationSound') !== 'false'
  )
  const [desktopNotificationEnabled, setDesktopNotificationEnabled] = useState(
    localStorage.getItem('desktopNotification') !== 'false'
  )
  const [fcmEnabled, setFCMEnabled] = useState(false)

  //  FCM 
  useEffect(() => {
    if (isOpen) {
      setFCMEnabled(fcmManager.isEnabled())
    }
  }, [isOpen])

  useEffect(() => {
    if (isOpen) {
      setTempNickname(user?.nickname || '')
      setAvatar(user?.avatar || '')
    }
  }, [isOpen, user])

  if (!isOpen) return null

  const handleSave = async () => {
    try {
      // 
      const updatedUser = {
        ...user,
        nickname: tempNickname || '',
        avatar: avatar
      }
      
      setUser(updatedUser)
      
      // TODO:  Firebase 
      console.log(' :', {
        nickname: tempNickname,
        avatar,
        notifications: notificationsEnabled
      })
      
      onClose()
    } catch (error) {
      console.error(':', error)
    }
  }

  // 
  const toggleSoundNotification = () => {
    const newSoundEnabled = !soundEnabled
    setSoundEnabled(newSoundEnabled)
    localStorage.setItem('notificationSound', newSoundEnabled.toString())
    toast.success(newSoundEnabled ? ' ' : ' ')
  }

  // 
  const toggleDesktopNotification = async () => {
    try {
      if (!desktopNotificationEnabled) {
        // 
        if (!('Notification' in window)) {
          toast.error(' ')
          return
        }

        const permission = await Notification.requestPermission()
        if (permission !== 'granted') {
          toast.error(' ')
          return
        }

        setDesktopNotificationEnabled(true)
        localStorage.setItem('desktopNotification', 'true')
        toast.success(' ')
        
        // 
        if (window.showNotification) {
          window.showNotification('', 'success')
        }
      } else {
        setDesktopNotificationEnabled(false)
        localStorage.setItem('desktopNotification', 'false')
        toast.success(' ')
      }
    } catch (error) {
      console.error(':', error)
      toast.error(' ')
    }
  }

  //  FCM 
  const toggleFCMNotification = async () => {
    try {
      if (!fcmEnabled) {
        //  FCM 
        console.log('  FCM ...')
        
        const token = await fcmManager.requestPermission(user?.uid)
        if (token) {
          setFCMEnabled(true)
          toast.success(' ', { duration: 5000 })
          
          // 
          if (window.showNotification) {
            window.showNotification(' FCM ', 'success', 8000)
          }
        } else {
          toast.error('  Token ')
        }
      } else {
        //  FCM 
        await fcmManager.disable()
        setFCMEnabled(false)
        toast.success(' ')
      }
    } catch (error) {
      console.error('FCM :', error)
      
      // 
      let errorMessage = ' '
      
      if (error.message.includes('')) {
        errorMessage = ` ${error.message}`
        // 
        if (window.showNotification) {
          window.showNotification(
            '     ', 
            'warning', 
            10000
          )
        }
      } else if (error.message.includes('HTTPS')) {
        errorMessage = '  HTTPS '
      } else if (error.message.includes('')) {
        errorMessage = ` ${error.message}`
      } else {
        errorMessage = ` ${error.message}`
      }
      
      toast.error(errorMessage, { duration: 6000 })
    }
  }

  // 
  const resetFCMPermission = () => {
    const instructions = `
 

1. /
2. 
3. 
4. 
5. 


- Chrome:       
- Firefox:       
- Safari:     
    `.trim()
    
    if (window.showNotification) {
      window.showNotification(instructions, 'info', 15000)
    } else {
      alert(instructions)
    }
  }

  const generateRandomAvatar = () => {
    const avatarUrl = `https://ui-avatars.com/api/?name=${encodeURIComponent(tempNickname || '')}&background=56c596&color=fff&size=128`
    setAvatar(avatarUrl)
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <div className="bg-white dark:bg-gray-800 rounded-xl shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
        {/*  */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200 dark:border-gray-700">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white"></h2>
          <button 
            onClick={onClose}
            className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
          >
            <X className="w-5 h-5 text-gray-500" />
          </button>
        </div>

        {/*  */}
        <div className="p-6 space-y-6">
          {/*  */}
          <div>
            <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4"></h3>
            
            {/*  */}
            <div className="flex items-center gap-4 mb-4">
              <img
                src={avatar || `https://ui-avatars.com/api/?name=${encodeURIComponent(tempNickname || '')}&background=56c596&color=fff&size=64`}
                alt=""
                className="w-16 h-16 rounded-full"
              />
              <div>
                <button
                  onClick={generateRandomAvatar}
                  className="flex items-center gap-2 px-3 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
                >
                  <Camera className="w-4 h-4" />
                  
                </button>
                <p className="text-xs text-gray-500 mt-1"></p>
              </div>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                
              </label>
              <input
                type="text"
                value={tempNickname}
                onChange={(e) => setTempNickname(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-700 dark:text-white"
                placeholder=""
                maxLength={20}
              />
            </div>
          </div>

          {/*  */}
          <div>
            <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4"></h3>
            
            {/*  */}
            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center gap-3">
                {soundEnabled ? (
                  <Volume2 className="w-5 h-5 text-green-500" />
                ) : (
                  <VolumeX className="w-5 h-5 text-gray-400" />
                )}
                <div>
                  <p className="font-medium text-gray-900 dark:text-white"></p>
                  <p className="text-sm text-gray-500"></p>
                </div>
              </div>
              <button
                onClick={toggleSoundNotification}
                className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 ${
                  soundEnabled ? 'bg-blue-500' : 'bg-gray-300'
                }`}
              >
                <span
                  className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                    soundEnabled ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
            </div>

            {/*  */}
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                {desktopNotificationEnabled ? (
                  <Bell className="w-5 h-5 text-green-500" />
                ) : (
                  <BellOff className="w-5 h-5 text-gray-400" />
                )}
                <div>
                  <p className="font-medium text-gray-900 dark:text-white"></p>
                  <p className="text-sm text-gray-500"></p>
                </div>
              </div>
              <button
                onClick={toggleDesktopNotification}
                className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 ${
                  desktopNotificationEnabled ? 'bg-blue-500' : 'bg-gray-300'
                }`}
              >
                <span
                  className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                    desktopNotificationEnabled ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
            </div>

            {/* FCM  */}
            <div className="mt-4 p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
              <div className="flex items-center justify-between mb-3">
                <div className="flex items-center gap-3">
                  {fcmEnabled ? (
                    <Smartphone className="w-5 h-5 text-blue-500" />
                  ) : (
                    <Smartphone className="w-5 h-5 text-gray-400" />
                  )}
                  <div>
                    <p className="font-medium text-gray-900 dark:text-white"> (FCM)</p>
                    <p className="text-sm text-gray-500"></p>
                  </div>
                </div>
                <button
                  onClick={toggleFCMNotification}
                  className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 ${
                    fcmEnabled ? 'bg-blue-500' : 'bg-gray-300'
                  }`}
                >
                  <span
                    className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                      fcmEnabled ? 'translate-x-6' : 'translate-x-1'
                    }`}
                  />
                </button>
              </div>
              
              {/* FCM  */}
              <div className="flex gap-2 text-xs">
                <button
                  onClick={resetFCMPermission}
                  className="px-2 py-1 bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors"
                >
                   
                </button>
                <button
                  onClick={async () => {
                    const diagnosis = await fcmManager.diagnoseEnvironment()
                    const status = `
 

 HTTPS: ${diagnosis.https ? '' : ' '}
 Service Worker: ${diagnosis.serviceWorker ? '' : ' '}
 : ${diagnosis.pushManager ? '' : ' '}  
  API: ${diagnosis.notification ? '' : ' '}
 Firebase Messaging: ${diagnosis.messaging ? '' : ' '}
 : ${diagnosis.permission}
 : ${diagnosis.userAgent.includes('Chrome') ? 'Chrome' : diagnosis.userAgent.includes('Firefox') ? 'Firefox' : diagnosis.userAgent.includes('Safari') ? 'Safari' : ''}
                    `.trim()
                    
                    if (window.showNotification) {
                      window.showNotification(status, 'info', 10000)
                    } else {
                      alert(status)
                    }
                  }}
                  className="px-2 py-1 bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors"
                >
                   
                </button>
              </div>
            </div>
          </div>

          {/*  */}
          <div>
            <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4"></h3>
            <div className="space-y-2 text-sm text-gray-600 dark:text-gray-400">
              <p> : 1.0.0</p>
              <p> React </p>
              <p> </p>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="flex gap-3 p-6 border-t border-gray-200 dark:border-gray-700">
          <button
            onClick={onClose}
            className="flex-1 px-4 py-2 text-gray-700 dark:text-gray-300 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
          >
            
          </button>
          <button
            onClick={handleSave}
            className="flex-1 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
          >
            
          </button>
        </div>
      </div>
    </div>
  )
}

export default SettingsModal