import React, { useEffect } from 'react'
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import { Toaster } from 'react-hot-toast'
import { useAuthStore } from './store/authStore'
import { auth, database } from './config/firebase'
import { onAuthStateChanged } from 'firebase/auth'
import { ref, get, remove } from 'firebase/database'
import { notificationManager } from './utils/notificationManager'
import { fcmManager } from './utils/fcmManager'
import { debugDatabase } from './utils/debugFirebase'

// 
import Landing from './pages/Landing'
import Login from './pages/Login'
import Chat from './pages/Chat'
import Debug from './pages/Debug'
import SplashScreen from './components/SplashScreen'
import ErrorBoundary from './components/ErrorBoundary'
import NotificationSystem from './components/NotificationSystem'

function App() {
  const { user, setUser, loading, setLoading } = useAuthStore()

  useEffect(() => {
    //  FCM
    const initializeFCM = async () => {
      try {
        await fcmManager.initialize()
        console.log(' FCM ')
      } catch (error) {
        console.error(' FCM :', error)
      }
    }

    initializeFCM()

    // Service Worker 
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.register('/sw.js')
        .then(() => console.log(' Service Worker '))
        .catch(error => console.log(' Service Worker :', error))
    }
    
    //  Firebase 
    const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
      if (firebaseUser) {
        let nickname = firebaseUser.displayName || ''
        let avatar = firebaseUser.photoURL || null
        
        //  Database 
        try {
          const userRef = ref(database, `users/${firebaseUser.uid}`)
          const userSnapshot = await get(userRef)
          
          if (userSnapshot.exists()) {
            const userData = userSnapshot.val()
            nickname = userData.nickname || nickname
            avatar = userData.avatar || avatar
          }
        } catch (error) {
          console.warn(':', error)
        }
        
        setUser({
          uid: firebaseUser.uid,
          email: firebaseUser.email,
          nickname: nickname,
          avatar: avatar,
          isAnonymous: firebaseUser.isAnonymous
        })
        
        console.log(' :', firebaseUser.uid, ':', nickname)
      } else {
        setUser(null)
      }
      setLoading(false)
    })

    return () => unsubscribe()
  }, [setUser, setLoading])

  // 
  useEffect(() => {
    const handleBeforeUnload = async (event) => {
      if (user && user.isAnonymous) {
        console.log('  - ')
        try {
          const userRef = ref(database, `users/${user.uid}`)
          await remove(userRef)
          console.log(' ')
        } catch (error) {
          console.error(' :', error)
        }
      }
    }

    // 
    window.addEventListener('beforeunload', handleBeforeUnload)
    
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload)
    }
  }, [user])

  //  Splash Screen
  if (loading) {
    return <SplashScreen />
  }

  return (
    <ErrorBoundary>
      <BrowserRouter
        future={{
          v7_startTransition: true,
          v7_relativeSplatPath: true
        }}
      >
        <div className="min-h-screen">
          <Routes>
            <Route path="/" element={user ? <Chat /> : <Landing />} />
            <Route path="/login" element={user ? <Chat /> : <Login />} />
            <Route path="/chat" element={user ? <Chat /> : <Login />} />
            <Route path="/debug" element={<Debug />} />
          </Routes>
          <Toaster 
            position="top-center"
            toastOptions={{
              duration: 3000,
              style: {
                background: '#2877b9',
                color: '#fff',
                borderRadius: '12px'
              }
            }}
          />
          <NotificationSystem />
        </div>
      </BrowserRouter>
    </ErrorBoundary>
  )
}

export default App