import React, { useState } from 'react'
import { debugDatabase, debugRoomMessages } from '../utils/debugFirebase'
import { useAuthStore } from '../store/authStore'

const DebugPage = () => {
  const { user } = useAuthStore()
  const [results, setResults] = useState('')
  const [loading, setLoading] = useState(false)

  const runFullDebug = async () => {
    setLoading(true)
    setResults('')
    
    try {
      console.log(' ...')
      await debugDatabase()
      setResults('')
    } catch (error) {
      setResults(`: ${error.message}`)
    } finally {
      setLoading(false)
    }
  }

  const debugSpecificRoom = async () => {
    const roomId = prompt(' ID (: casual, dating, hookup):')
    if (!roomId) return

    setLoading(true)
    try {
      const messages = await debugRoomMessages(roomId)
      if (messages) {
        setResults(` ${roomId}  ${Object.keys(messages).length} `)
      } else {
        setResults(` ${roomId} `)
      }
    } catch (error) {
      setResults(`: ${error.message}`)
    } finally {
      setLoading(false)
    }
  }



  return (
    <div className="min-h-screen bg-gray-100 dark:bg-gray-900 p-4">
      <div className="max-w-2xl mx-auto">
        <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-6">
          Firebase 
        </h1>

        {user && (
          <div className="mb-6 p-4 bg-white dark:bg-gray-800 rounded-lg shadow">
            <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
              
            </h2>
            <p className="text-gray-600 dark:text-gray-300">
              UID: {user.uid}
            </p>
            <p className="text-gray-600 dark:text-gray-300">
              : {user.nickname}
            </p>
          </div>
        )}

        <div className="space-y-4">
          <button
            onClick={runFullDebug}
            disabled={loading}
            className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
          >
            {loading ? '...' : ' ()'}
          </button>

          <button
            onClick={debugSpecificRoom}
            disabled={loading}
            className="w-full px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50"
          >
            
          </button>


        </div>

        {results && (
          <div className="mt-6 p-4 bg-white dark:bg-gray-800 rounded-lg shadow">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
              
            </h3>
            <pre className="text-sm text-gray-600 dark:text-gray-300 whitespace-pre-wrap">
              {results}
            </pre>
          </div>
        )}

        <div className="mt-6 p-4 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg">
          <h3 className="text-lg font-semibold text-yellow-800 dark:text-yellow-200 mb-2">
            
          </h3>
          <p className="text-yellow-700 dark:text-yellow-300 text-sm">
             "permission_denied"  Firebase Console 
             FIREBASE_SETUP.md 
          </p>
        </div>
      </div>
    </div>
  )
}

export default DebugPage