import { useEffect, useRef } from 'react'
import { 
  ref, 
  onValue, 
  push, 
  set, 
  onDisconnect,
  serverTimestamp,
  query,
  orderByChild,
  limitToLast
} from 'firebase/database'
import { database } from '../config/firebase'
import { useAuthStore } from '../store/authStore'
import { useChatStore } from '../store/chatStore'
import { notificationManager } from '../utils/notificationManager'

// 
const getRoomDisplayName = (roomId) => {
  const roomNames = {
    'casual': '',
    'dating': '', 
    'hookup': '',
    'confession': '',
    'general': '',
    'random': ''
  }
  return roomNames[roomId] || ` ${roomId}`
}

export const useFirebaseChat = (roomId) => {
  const { user } = useAuthStore()
  const { setMessages, addMessage, clearMessages } = useChatStore()
  const lastMessageCountRef = useRef(0)

  useEffect(() => {
    if (!user || !roomId) {
      console.log(' useFirebaseChat: ', { user: !!user, roomId })
      return
    }

    console.log(`  ${roomId} ...`)

    // 
    clearMessages()

    // 
    const messagesRef = ref(database, `groupChats/${roomId}/messages`)
    
    const unsubscribe = onValue(messagesRef, (snapshot) => {
      try {
        const data = snapshot.val()
        console.log(`  ${roomId} :`, data)
        
        if (data) {
          const messages = Object.entries(data).map(([id, message]) => {
            // 
            if (Math.random() < 0.1) { //  10% 
              console.log(`  ${id} :`, {
                timestamp: message.timestamp,
                time: message.time,
                rawMessage: message
              })
            }
            
            return {
              id,
              ...message,
              // 
              timestamp: message.timestamp || message.time || Date.now()
            }
          })
          // 
          messages.sort((a, b) => {
            const timeA = a.timestamp || a.time || 0
            const timeB = b.timestamp || b.time || 0
            return timeA - timeB
          })
          
          //  ()
          const currentMessageCount = messages.length
          const previousMessageCount = lastMessageCountRef.current
          
          // 
          if (currentMessageCount > previousMessageCount && previousMessageCount > 0) {
            const newMessages = messages.slice(previousMessageCount)
            
            // 
            newMessages.forEach(message => {
              const messageFrom = message.from || message.userId || message.uid
              if (messageFrom !== user.uid) {
                // 
                const senderName = message.user || message.nickname || ''
                const roomName = getRoomDisplayName(roomId)
                
                console.log(`  ${senderName}  (${roomName}):`, message.text)
                
                // 
                notificationManager.showMessageNotification(
                  `${senderName} (${roomName})`,
                  message.text,
                  'group'
                )
                
                // 
                notificationManager.playNotificationSound()
                
                // 
                if (window.showNotification) {
                  window.showNotification(` ${senderName} (${roomName}): ${message.text}`, 'info', 6000)
                }
                
                //  ()
                if (Math.random() < 0.3) { //  30% 
                  notificationManager.playNotificationSound()
                }
              }
            })
          }
          
          lastMessageCountRef.current = currentMessageCount
          console.log(`  ${roomId}  ${messages.length} `)
          setMessages(messages)
        } else {
          console.log(`  ${roomId} `)
          lastMessageCountRef.current = 0
          setMessages([])
        }
      } catch (error) {
        console.error(`  ${roomId} :`, error)
        setMessages([])
      }
    }, (error) => {
      console.error(` Firebase onValue  ( ${roomId}):`, error)
      if (error.code === 'PERMISSION_DENIED') {
        console.error('  -  Firebase !')
      }
    })

    return () => {
      try {
        console.log(`  ${roomId} `)
        unsubscribe()
      } catch (error) {
        console.error('Firebase unsubscribe error:', error)
      }
    }
  }, [user, roomId, setMessages, clearMessages])

  // 
  const sendMessage = async (messageData) => {
    if (!user || !roomId) return

    const messagesRef = ref(database, `groupChats/${roomId}/messages`)
    
    const message = {
      ...messageData,
      sender: {
        uid: user.uid,
        nickname: user.nickname || '',
        avatar: user.avatar || null
      },
      timestamp: Date.now(), //  Date.now()  serverTimestamp()
      room: roomId
    }

    try {
      await push(messagesRef, message)
    } catch (error) {
      console.error(':', error)
      throw error
    }
  }

  return { sendMessage }
}