// 
class NotificationManager {
  constructor() {
    this.isSupported = 'Notification' in window
  }

  // 
  get hasPermission() {
    return this.isSupported && Notification.permission === 'granted'
  }

  // 
  async requestPermission() {
    if (!this.isSupported) {
      console.warn(' ')
      return false
    }

    try {
      const permission = await Notification.requestPermission()
      return permission === 'granted'
    } catch (error) {
      console.error(' :', error)
      return false
    }
  }

  // 
  showNotification(title, body = '', onClick = null) {
    if (!this.hasPermission) {
      // 
      if (window.showNotification) {
        window.showNotification(`${title}: ${body}`, 'info')
      }
      return null
    }

    const options = {
      body,
      icon: '/icon-512.png',
      tag: 'lalaland-message',
      requireInteraction: false,
      silent: false
    }

    try {
      const notification = new Notification(title, options)
      
      // 
      notification.onclick = () => {
        window.focus()
        if (onClick) onClick()
        notification.close()
      }

      //  (5)
      setTimeout(() => {
        notification.close()
      }, 5000)

      return notification
    } catch (error) {
      console.error(' :', error)
      // 
      if (window.showNotification) {
        window.showNotification(`${title}: ${body}`, 'info')
      }
      return null
    }
  }

  // 
  showMessageNotification(senderName, message, type = 'private') {
    const title = type === 'private' ? 
      ` ${senderName} ` : 
      ` ${senderName} `
    
    const body = message.length > 50 ? message.substring(0, 50) + '...' : message
    
    return this.showNotification(title, body, () => {
      // 
      window.focus()
    })
  }

  // 
  playNotificationSound() {
    const soundEnabled = localStorage.getItem('notificationSound') !== 'false'
    if (!soundEnabled) return

    try {
      const audioContext = new (window.AudioContext || window.webkitAudioContext)()
      const oscillator = audioContext.createOscillator()
      const gainNode = audioContext.createGain()
      
      oscillator.connect(gainNode)
      gainNode.connect(audioContext.destination)
      
      oscillator.frequency.value = 800
      oscillator.type = 'sine'
      
      gainNode.gain.setValueAtTime(0.1, audioContext.currentTime)
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.3)
      
      oscillator.start()
      oscillator.stop(audioContext.currentTime + 0.3)
    } catch (error) {
      console.log(' :', error)
    }
  }
}

// 
export const notificationManager = new NotificationManager()

// 
export const requestNotificationPermission = async () => {
  return await notificationManager.requestPermission()
}

export default notificationManager