// fcmManager.js - Firebase Cloud Messaging 
import { getToken, onMessage } from 'firebase/messaging'
import { messaging } from '../config/firebase'
import { ref, set, push } from 'firebase/database'
import { database } from '../config/firebase'

// VAPID 
const VAPID_KEY = '6VcIS3LHfhdZSsTZc_dy9gJVdNOZaUnRMFOv7Ys0G0I'

class FCMManager {
  constructor() {
    this.fcmToken = null
    this.isSupported = this.checkFCMSupport()
    this.isInitialized = false
  }

  //  FCM 
  checkFCMSupport() {
    return !!(
      messaging && 
      'serviceWorker' in navigator && 
      'PushManager' in window &&
      'Notification' in window
    )
  }

  //  FCM
  async initialize() {
    if (!this.isSupported) {
      console.warn(' FCM ')
      return false
    }

    try {
      //  Service Worker
      await this.registerServiceWorker()
      
      // 
      this.setupForegroundListener()
      
      this.isInitialized = true
      console.log(' FCM ')
      return true
    } catch (error) {
      console.error(' FCM :', error)
      return false
    }
  }

  //  Service Worker
  async registerServiceWorker() {
    if ('serviceWorker' in navigator) {
      try {
        const registration = await navigator.serviceWorker.register('/firebase-messaging-sw.js')
        console.log(' FCM Service Worker :', registration)
        return registration
      } catch (error) {
        console.error(' FCM Service Worker :', error)
        throw error
      }
    }
  }

  //  FCM 
  async diagnoseEnvironment() {
    const diagnosis = {
      https: location.protocol === 'https:' || location.hostname === 'localhost',
      serviceWorker: 'serviceWorker' in navigator,
      pushManager: 'PushManager' in window,
      notification: 'Notification' in window,
      messaging: !!messaging,
      permission: Notification.permission,
      userAgent: navigator.userAgent,
      url: window.location.href
    }

    console.log(' FCM :', diagnosis)
    return diagnosis
  }

  //  FCM Token
  async requestPermission(userId = null) {
    // 
    const diagnosis = await this.diagnoseEnvironment()
    
    if (!this.isSupported) {
      const missingFeatures = []
      if (!diagnosis.serviceWorker) missingFeatures.push('Service Worker')
      if (!diagnosis.pushManager) missingFeatures.push('Push Manager')
      if (!diagnosis.notification) missingFeatures.push('Notification API')
      if (!diagnosis.messaging) missingFeatures.push('Firebase Messaging')
      
      console.error(' FCM :', missingFeatures.join(', '))
      throw new Error(`: ${missingFeatures.join(', ')}`)
    }

    if (!this.isInitialized) {
      console.warn(' FCM ...')
      const initSuccess = await this.initialize()
      if (!initSuccess) {
        throw new Error('FCM ')
      }
    }

    //  HTTPS 
    if (!diagnosis.https) {
      throw new Error(' HTTPS  localhost')
    }

    try {
      // 
      console.log(' :', Notification.permission)

      // 
      if (Notification.permission === 'denied') {
        throw new Error('')
      }

      // 
      console.log(' ...')
      const permission = await Notification.requestPermission()
      console.log(' :', permission)

      if (permission !== 'granted') {
        if (permission === 'denied') {
          throw new Error('')
        } else {
          throw new Error('')
        }
      }

      //  FCM Token
      console.log('  FCM Token...')
      const token = await getToken(messaging, {
        vapidKey: VAPID_KEY
      })

      if (token) {
        console.log(' FCM Token :', token)
        this.fcmToken = token
        
        //  ID token 
        if (userId) {
          await this.saveFCMToken(userId, token)
        }
        
        return token
      } else {
        console.warn('  FCM Token')
        return null
      }
    } catch (error) {
      console.error(' FCM :', error)
      return null
    }
  }

  //  FCM Token 
  async saveFCMToken(userId, token) {
    try {
      const tokenRef = ref(database, `fcmTokens/${userId}`)
      await set(tokenRef, {
        token,
        timestamp: Date.now(),
        userAgent: navigator.userAgent
      })
      console.log(' FCM Token ')
    } catch (error) {
      console.error('  FCM Token :', error)
    }
  }

  // 
  setupForegroundListener() {
    if (!messaging) return

    onMessage(messaging, (payload) => {
      console.log(' FCM :', payload)

      const { title, body } = payload.notification || {}
      
      // 
      if (window.showNotification) {
        window.showNotification(
          title ? `${title}: ${body}` : body || '',
          'info',
          6000
        )
      }

      // 
      this.playNotificationSound()

      // 
      if (document.hidden || document.visibilityState === 'hidden') {
        this.showDesktopNotification(title || '', body || '')
      }
    })
  }

  // 
  showDesktopNotification(title, body) {
    if (Notification.permission === 'granted') {
      const notification = new Notification(title, {
        body,
        icon: '/icon-512.png',
        badge: '/icon-512.png',
        tag: 'lalaland-fcm'
      })

      notification.onclick = () => {
        window.focus()
        notification.close()
      }

      // 5
      setTimeout(() => {
        notification.close()
      }, 5000)
    }
  }

  // 
  playNotificationSound() {
    const soundEnabled = localStorage.getItem('notificationSound') !== 'false'
    if (!soundEnabled) return

    try {
      const audioContext = new (window.AudioContext || window.webkitAudioContext)()
      const oscillator = audioContext.createOscillator()
      const gainNode = audioContext.createGain()
      
      oscillator.connect(gainNode)
      gainNode.connect(audioContext.destination)
      
      oscillator.frequency.value = 800
      oscillator.type = 'sine'
      
      gainNode.gain.setValueAtTime(0.1, audioContext.currentTime)
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.3)
      
      oscillator.start()
      oscillator.stop(audioContext.currentTime + 0.3)
    } catch (error) {
      console.log(' :', error)
    }
  }

  //  Token
  getCurrentToken() {
    return this.fcmToken
  }

  // 
  isEnabled() {
    return !!(this.fcmToken && Notification.permission === 'granted')
  }

  //  FCM
  async disable() {
    try {
      if (messaging && this.fcmToken) {
        //  token
        console.log(' FCM ')
      }
      this.fcmToken = null
    } catch (error) {
      console.error('  FCM :', error)
    }
  }
}

//  FCM 
export const fcmManager = new FCMManager()

export default fcmManager