// 
import { ref, get, remove, update } from 'firebase/database'
import { database } from '../config/firebase'

export const cleanupStaleUsers = async () => {
  console.log(' ...')
  
  try {
    const usersRef = ref(database, 'users')
    const snapshot = await get(usersRef)
    
    if (!snapshot.exists()) {
      console.log(' ')
      return
    }
    
    const users = snapshot.val()
    const now = Date.now()
    const STALE_TIME = 5 * 60 * 1000 // 5
    
    let cleanedCount = 0
    
    for (const [uid, userData] of Object.entries(users)) {
      if (!userData) continue
      
      // 
      const isStale = !userData.lastSeen || (now - userData.lastSeen > STALE_TIME)
      
      if (userData.isOnline && isStale) {
        console.log(` : ${uid} (${userData.nickname || ''})`)
        
        if (userData.isAnonymous) {
          // 
          await remove(ref(database, `users/${uid}`))
          console.log(` : ${uid}`)
        } else {
          // 
          await update(ref(database, `users/${uid}`), {
            isOnline: false,
            lastSeen: now
          })
          console.log(` : ${uid}`)
        }
        cleanedCount++
      }
    }
    
    console.log(`  ${cleanedCount} `)
    return cleanedCount
    
  } catch (error) {
    console.error(' :', error)
    throw error
  }
}

// 
export const startPeriodicCleanup = () => {
  console.log(' ...')
  
  // 
  cleanupStaleUsers()
  
  // 5
  const intervalId = setInterval(cleanupStaleUsers, 5 * 60 * 1000)
  
  return () => {
    console.log(' ')
    clearInterval(intervalId)
  }
}